@import "../../css-config/mixins.scss";




.main-menu{
    height: 100%;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 999;
    display: flex;
    justify-content: flex-end;
    

    .opac-layer{
        background-color: rgba(0, 0, 0, 0.66);
        height: 100%;
        width: 100%;
        position: absolute;
        top: 0;
        left: 0;

        h1{
            color: white;
            font-size: 55px;
            font-weight: 700;
            line-height: 100px;
            transform: translate(100px,200px);
        }
    }


    .menu-content{

        width: 50%;
        height: 100%;
        background-color: rgba(18, 9, 9, 0.879);
        z-index: 1;
        // padding: 0 20px;
        animation: openmodel .5s ;


    }
}


@keyframes openmodel{
    0%{width: 0;}
    100%{width: 50%;}
}