@import "../../css-config/mixins.scss";

.category-page{
    margin: 25px 10px;
    @include md{
        margin:  50px 20px;
    }


    .category-heading{
        margin-bottom: 20px;
        font-size: 18px;
        font-weight: 500;
        text-transform: uppercase;
        @include md{
            margin-bottom: 35px;
            font-size: 24px;
        }

        &:after{
            content: "";
            display: block;
            margin-top: 5px;
            width: 50px;
            height: 3px;
            background-color: #8e2de2;
            @include md{
                margin-top: 10px;
            }
        }
    }

    .select-filter{
        padding: 8px;
        float: right;
      

        @include md{
            margin-right: 20px;
        }

        option{
           line-height: 30px;
          
        }
    }


    .filter-product{
        float: right;
       
    }

    .category-products{
        display: grid;
        gap:10px;
    grid-template-columns: auto auto;
    
    
   
    // gap: 10px;

    @include md{ 
        grid-template-columns: auto auto auto auto;
        padding: 10px;
        gap: 30px 10px;

    }

    .category-container{
       

        .product-name{
            font-size: 21px;
            font-weight: 500;
            text-overflow: ellipsis;
            padding: 0 10px;
            
        }

        .product-price{
            font-size: 17px;
            font-weight: 500;
            text-overflow: ellipsis;
            padding: 0 10px;
            
        }

    

        .product-page{

            background-color: rgba(0,0,0,0.1);
        
        overflow: hidden;
        cursor: pointer;
        margin-bottom: 10px;

        img{
            width: 100%;
            height: auto;
           
            display: block;
            transition: all ease 0.3s;

            @include md{

            
            
            

            &:hover{
              
                    transform: scale(1.5);
               
            }
        }

           

        }
        }

    }
    


        

    }
   
}