@import "../../css-config/mixins.scss";


.wish-model{
    height: 100%;
    width: 100%;
    z-index: 999;
   
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;

    .opac-layer{
        height: 100%;
        width: 100%;
        background-color: rgba(0,0,0,0.5);
        // z-index: 1;
        position: absolute;
        top: 0;
        left: 0;
    }

    .wish-content{

        height: 100%;
        width: 100%;
        background-color: rgb(55, 153, 238);
        z-index: 1;
        animation: openwish 0.5s;

        .close{
           position: absolute;
           top: 50%;
           left: 50%;
           transform: translate(-50%,-50%);
           font-size: 44px;
        }

    }


}


@keyframes openwish{
    0%{ transform: scale(0)}
    100%{ transform: scale(1);}
}