@import "../../css-config/mixins.scss";

.main-header{
    width: 100%;
    padding: 0 20px;
    background-color: #ff0000;
    color: white;
    border-bottom: 1px solid rgba(0,0,0,0.1);
    z-index: 99; 
    
    @include md{
        padding: 0 40px
    }
    
    .header-content{
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 50px;
        max-width: 1200px;
        margin: 0 auto;
        @include md {
            height: 80px;
            
        }
        .left{
            list-style-type: none;
            display: none;
            margin: auto 0;
            
            gap:25px;
            @include md{
                display: flex;
                

            }
            li{
                font-size: 17px;
                font-weight: 600;
                text-transform:uppercase;
                cursor: pointer;
                transition: 0.5s ease all;

                &:hover{
                    transform: scale(1.2);
                    color: rgba(228, 229, 220, 0.808)
                }
                

            };
            a{
                text-decoration: none;
                color: white;
            }
           


        }

        .center{
            font-size: 22px;
            color: rgb(255, 255, 255);
            font-weight: 700;
            cursor: pointer;
            @include md{
                font-size: 34px;
                position: absolute;
                color: rgb(255, 255, 255);
                left: 50%;
                transform: translateX(-50%);
                transition: 1s ease all;
                overflow: hidden;
                line-height: 50px;

                &:hover::after{
                    content: " - Men's Wear";
                    font-size: 22px;
                    font-weight: 100;
                    color: rgb(224, 221, 217);
                   
 
                }
               
            }

            

           

        }

        .right{
            display: flex;
            align-items: center;
            gap: 20px;
            @include md{
                gap:25px
            }

            svg{
                font-size: 20px;
                cursor: pointer;
                @include md{
                    font-size: 24px;
                    transition: transform .5s;

                    &:hover{
                        transform: matrix(1.2, 0, 0, 1, 0, 0);
                        color: rgba(228, 229, 220, 0.808)
                       
                        
                    }
                }
            }
            .cart-icon{
                position: relative;
                span{
                    min-width: 20px;
                    text-align: center;
                    background-color: #8e2de2;
                    padding: 2.5px;
                    position: absolute;
                    top: -5px;
                    right: -12px;
                    font-size: 12px;
                    line-height: 1;
                    border-radius: 10px;
                }
            }

        }
    }

    &.sticky-header{
        position: sticky;
        top: 0;
        animation: stickyHeader 0.3s ease forwards;
    }
}

@keyframes stickyHeader{
    0% {transform:translateY(-80px);}
    100%{transform: translateY(0);}
}
